@include tablet {
    .iconos-nosotros {
        @include grid(3, 4.5rem);
    }
}

.icono {
    text-align: center;
    

    img {
        height: 10rem;
    }

    h3 {
        text-transform: uppercase;
        font-family: $fuente_secundaria;
        font-size: 2.7rem;
    }

    p {
        font-weight: $delgada;
    }

    @include desktopMax {
        p {
            font-size: 1.8rem;
        }
    }
}