.seccion {
    margin-top: calc($separacion / 2);
    margin-bottom: calc($separacion / 2);
}
.contenido-centrado {
    max-width: 100rem;
}
.contenido-centrado-usuario {
    max-width: 70rem;
}

.alinear-derecha {
    display: flex;
    justify-content: flex-end;
}

.padding-titulos h1 {
    padding: 1rem;
}

.imagen-small {
    width: 20%;
    border-radius: 0;
    margin-bottom: 2rem;
}

.w-100 {
    width: 100%;
}

@include tabletMax {
    .imagen-small {
        width: 35%;
    }
}