/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 140rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 1.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
  padding: 4rem 0 3rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.contenido-centrado {
  max-width: 100rem;
}

.contenido-centrado-usuario {
  max-width: 70rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.padding-titulos h1 {
  padding: 1rem;
}

.imagen-small {
  width: 20%;
  border-radius: 0;
  margin-bottom: 2rem;
}

.w-100 {
  width: 100%;
}

@media (max-width: 768px) {
  .imagen-small {
    width: 35%;
  }
}
.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFA;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #E1E1E1;
}
.dark-mode .texto-nosotros blockquote {
  color: #E08709;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .icono-propiedades {
  filter: invert(98%);
}
.dark-mode .informacion {
  background-color: #333333;
}
.dark-mode .informacion label {
  color: #FFFFFA;
}
.dark-mode .informacion-usuario {
  background-color: #333333;
}
.dark-mode .informacion-usuario label {
  color: #FFFFFA;
}
.dark-mode .propiedades {
  color: #FFFFFA;
  box-shadow: rgba(151, 151, 151, 0.5) 1px -1px 7px 2px;
}
.dark-mode .propiedades thead th {
  background-color: #1a1a1a;
}
.dark-mode .propiedades .cuerpo-table td {
  background-color: #1a1a1a;
}
.dark-mode .propiedades .cuerpo-table .propiedades-fila:first-child {
  box-shadow: 0 -1px 4px rgba(151, 151, 151, 0.5);
}
/**UI**/
.header {
  background-color: #333333;
  background-blend-mode: soft-light;
  padding: 1rem 0 3rem 0;
}
@media (min-width: 768px) {
  .header.inicio {
    background-position: center center;
    background-size: cover;
    min-height: 55rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 55rem;
  }
}

.barra img {
  width: 65%;
  border-radius: 0;
}
.barra .titulo {
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .barra .titulo {
    padding-bottom: 0;
  }
}

.no-webp .inicio {
  background-image: url(../img/header1.jpg);
}

.webp .inicio {
  background-image: url(../img/header1.webp);
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #FFFFFA;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    font-size: 3.5rem;
    text-align: left;
    font-weight: 700;
    line-height: 2;
    max-width: 60rem;
    text-transform: capitalize;
  }
}
.contenido-header h1 span {
  color: #E08709;
  font-weight: bold;
}

.footer {
  margin: 0;
  margin-top: 2.5rem;
  padding: 1.5rem;
  background-color: #333333;
  margin-top: auto;
  font-size: 1.8rem;
}
.footer .contenedor-footer {
  padding-top: 1.5rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.footer .titulo-footer {
  width: 15%;
}
.footer .copyright {
  color: #FFFFFA;
  text-align: center;
  font-size: 1.5rem;
  margin: 3rem 0 1rem;
}
.footer a, .footer p {
  color: black;
}

.navegacion-footer-abajo {
  justify-content: center;
}

.barra {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 2.5rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (max-width: 768px) {
  .barra .titulo {
    padding-bottom: 0;
  }
}

@media (max-width: 768px) {
  .menu-responsive {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 2s ease-in-out, opacity 0.1s linear, height 0.1s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFA;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #E08709;
}

.navegacion a:last-child:hover {
  color: #FFFFFA;
}

.navegacion-footer-abajo {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.navegacion-footer-abajo a {
  font-size: 1.5rem;
  color: #FFFFFA;
  text-decoration: none;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion-footer-abajo a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion-footer-abajo a:last-of-type {
    margin-right: 0;
  }
}

/**MENU HAMBUGUESA**/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
  padding-top: 2rem;
}

/**DARK MODE**/
.derecha {
  display: flex;
  align-items: center;
}
@media (max-width: 768px) {
  .derecha {
    flex-direction: column;
    justify-content: center;
  }
}
.derecha .dark-mode-boton {
  max-width: 4rem;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-right: 2rem;
    margin-top: 2rem;
  }
}
@media (max-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 2rem;
  }
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 4.5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
  font-family: "Oswald", sans-serif;
  font-size: 2.7rem;
}
.icono p {
  font-weight: 300;
}
@media (max-width: 1024px) {
  .icono p {
    font-size: 1.8rem;
  }
}

@media (min-width: 1024px) {
  .contenedor-anuncios {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 1024px) {
  .contenedor-anuncios {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.anuncio {
  overflow: hidden;
  background-color: #eeeeee;
  border: 1px solid #cdcdcd;
  border-top-left-radius: 2.1rem;
  border-top-right-radius: 2.1rem;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
}
@media (max-width: 768px) {
  .anuncio {
    margin-bottom: 3rem;
  }
}
@media (max-width: 480px) {
  .anuncio {
    margin-bottom: 3.5rem;
  }
}
.anuncio .imagen-anuncio {
  height: 28rem;
}

.contenido-anuncio {
  padding: 1.5rem 2rem 2rem 2rem;
  text-align: center;
}
.contenido-anuncio h3, .contenido-anuncio p {
  margin: 0;
  overflow-wrap: break-word;
}
.contenido-anuncio h3 {
  font-family: "Oswald", sans-serif;
  font-size: 3rem;
  font-weight: 200;
}
.contenido-anuncio p {
  font-weight: lighter;
  font-size: 1.8rem;
}
.contenido-anuncio .precio {
  color: #71b100;
  font-weight: bold;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  justify-content: space-around;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  display: flex;
}
.iconos-caracteristicas img {
  margin-right: 1rem;
  border-radius: 0;
}

.precio-bloque {
  width: 20rem;
  margin: 0 auto;
  border-radius: 2rem;
}
.precio-bloque p {
  color: #71b100;
  font-weight: bold;
  font-size: 3.2rem;
}

.imagen {
  padding-bottom: 2.5rem;
}
.imagen img {
  max-height: 70rem;
}

.precio-anuncio {
  text-align: center;
  margin: 0;
}

.descripcion {
  text-align: justify;
  margin: 0;
}

.bloque {
  background-color: #333333;
  border-radius: 2rem;
  padding: 1rem;
  margin: 2.5rem 0 2.5rem 0;
}
.bloque img {
  width: 5rem;
}

.contenido-centrado img {
  border-radius: 2rem;
}

.iconos-anuncios {
  margin: 0;
}
.iconos-anuncios p {
  color: #71b100;
}

picture, img {
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
}

.imagen-anuncios img {
  height: 27.5rem;
}

