.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 4.2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFA;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFA;
  text-align: center;
}

.seccion-inferior {
  width: 60%;
}

.blog h3, .testimoniales h3 {
  font-size: 3.4rem;
}

.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
.entrada-blog img {
  border-radius: 2rem;
}

.informacion-meta span {
  color: #71b100;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 1rem;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #E08709;
  margin-top: 1rem;
}
.texto-entrada p {
  font-size: 1.8rem;
}
.texto-entrada span {
  color: #71b100;
}

.imagen-formulario img {
  border-radius: 2rem;
}

.formulario p {
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 700;
  margin: 2rem 0;
  color: #E08709;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  color: #000000;
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not(input[type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFA;
  border: 1px solid #E1E1E1;
  border-radius: 1rem;
}
.formulario input:not(input[type=radio]),
.formulario textarea,
.formulario select {
  margin-bottom: 2rem;
}
.formulario fieldset {
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 300;
  margin-bottom: 2rem;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.informacion {
  border: none;
  background-color: #E1E1E1;
  border-radius: 2rem;
}
.informacion legend {
  background-color: #E08709;
  border-radius: 2rem;
  width: 20rem;
  padding: 0.3rem;
  color: #FFFFFA;
}

.informacion-propiedad legend {
  background-color: #E08709;
  border-radius: 2rem;
  width: 26rem;
  padding: 0.3rem;
  color: #FFFFFA;
}

.informacion-contacto legend {
  background-color: #E08709;
  border-radius: 2rem;
  width: 23rem;
  padding: 0.3rem;
  color: #FFFFFA;
}

.forma-contacto label {
  color: #000000;
}

.boton-naranja {
  cursor: pointer;
}

.formulario-usuario p {
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 700;
  margin: 2rem 0;
  color: #71b100;
}
.formulario-usuario legend {
  text-align: center;
  font-size: 2rem;
  color: #333333;
}
.formulario-usuario label {
  color: #000000;
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  width: 30rem;
}
.formulario-usuario input:not(input[type=submit]) {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFA;
  border: 1px solid #E1E1E1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

.informacion-usuario {
  border: none;
  background-color: #E1E1E1;
  border-radius: 2rem;
  margin-top: 5rem;
}
.informacion-usuario legend {
  font-size: 2.5rem;
  background-color: #E08709;
  border-radius: 3rem;
  width: 25rem;
  padding: 0.3rem;
  color: #FFFFFA;
  text-transform: uppercase;
}

.boton-naranja {
  cursor: pointer;
}

/**P√°ginas Internas**/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 1024px) {
  .contenido-nosotros p {
    font-size: 1.8rem;
  }
}
.contenido-nosotros p {
  font-weight: 300;
  text-align: justify;
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding-left: 1rem 0 3rem 0;
}
@media (max-width: 1024px) {
  .texto-nosotros blockquote {
    font-size: 2rem;
  }
}

@media (max-width: 1024px) {
  .imagen {
    display: flex;
    align-items: center;
  }
}
.imagen img {
  border-radius: 2rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFA;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(185, 36, 36);
  border-radius: 1.5rem;
}
.alerta.exito {
  background-color: #71b100;
}

table.propiedades {
  margin-top: 2rem;
  width: 100%;
  border-spacing: 0;
  margin-bottom: 10%;
}
table.propiedades thead {
  background-color: #E08709;
  margin-bottom: 2rem;
  border-radius: 2px;
}
table.propiedades thead th {
  color: #FFFFFA;
  padding: 2rem;
}
table.propiedades td {
  background-color: whitesmoke;
  text-align: center;
  padding: 2rem;
}
table.propiedades td img {
  height: 10rem;
}
table.propiedades a {
  border-radius: 0;
}
table.propiedades a:last-of-type {
  margin-top: 1rem;
}
table.propiedades .imagen-tabla {
  width: 15rem;
  border-radius: 2rem;
  margin: 0 auto;
}

.imagen-peque√±a {
  width: 25rem;
  border-radius: 2rem;
}

.division {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.division .boton-verde {
  margin-top: 0;
}

.admin-titulo {
  margin-bottom: 10%;
}

.vendedores-titulo, .propiedades-titulo {
  text-align: left;
  font-weight: 700;
  font-size: 3rem;
  text-transform: uppercase;
  margin: 0;
}

.formulario-crear, .formulario-actualizar {
  margin-top: 1rem;
}

.informacion-general-crear legend {
  width: 25rem;
  text-align: center;
}

.informacion-propiedad-crear legend {
  width: 27rem;
  text-align: center;
}

.informacion-vendedor-crear legend {
  width: 15rem;
  text-align: center;
}